html,
body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

.image-selection-page {
  display: flex;
  justify-content: center;
  align-items: stretch;
  height: auto;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-image: url(./assets/images/val.png); /* 여기에 이미지 경로를 추가하세요. */
  position: relative;
}
.image-list {
  flex: 1;
  display: flex;
  flex-direction: column; /* 변경된 속성 */
  justify-content: center;
  align-items: center;
  gap: 50px; /* 변경된 속성 */
  position: relative;
  padding-bottom: 50px; /* 추가된 속성 */
}
.image-selection-page::before {
  content: "";
  background-color: black;
  opacity: 0.3;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main-image-container {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%; /* 추가된 속성 */
}

.main-image-container img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.main-image-container h1 {
  position: fixed;
  top: 20%;
  left: 10%;
  color: white;
  font-size: 5em;
  z-index: 2;
}
.main-image-container p {
  position: fixed;
  top: 40%;
  left: 10%;
  color: white;
  font-size: 1.2em;
  z-index: 2;
}

.image-list {
  flex: 1;
  display: flex;
  flex-direction: column; /* 변경된 속성 */
  justify-content: center;
  align-items: center;
  gap: 50px; /* 변경된 속성 */
  position: relative;
}
.developer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 3;
  width: 250px; /* 고정 너비를 설정해주세요 */
  height: 180px; /* 고정 높이를 설정해주세요 */
  /* margin-bottom: 20px; 추가된 속성 */
}

.view-all-projects {
  position: fixed;
  top: 55%;
  left: 10%;
  border: 2px solid white;
  border-radius: 5px;
  padding: 10px 20px;
  color: white;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s;
}

.view-all-projects:hover {
  background-color: rgba(255, 255, 255, 0.5);
  transform: scale(1.3);
  transition: transform 0.3s;
}

.developer-container img {
  width: 100px;
  height: auto;
  border-radius: 50%;
}

.developer-container h2,
.developer-container p {
  color: aliceblue;
  margin: 0;
  padding: 0;
  text-align: center;
}
.developer-container:hover {
  transform: scale(1.1);
  transition: transform 0.3s;
}
.developer-container .greeting-container {
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.developer-container:hover .greeting-container {
  opacity: 1;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.fade-in {
  animation: fadeIn 1s ease-in-out forwards;
}

.fade-out {
  animation: fadeOut 1s ease-in-out forwards;
}

@media (max-width: 768px) {
  .view-all-projects {
    position: fixed;
    top: 2%;
    left: 30%;
    z-index: 10;
    /* transform: translateX(-50%); */
  }
  .view-all-projects:hover {
    pointer-events: none;
    transform: none !important;
    transition: none !important;
  }
  .image-selection-page {
    flex-direction: column;
  }
  .developer-container {
    margin-bottom: 50px; /* 추가된 속성 */
  }
  .developer-container {
    width: 100px;
    height: 100px;
  }
  .developer-container .greeting-container {
    display: none;
  }
  .main-image-container h1 {
    opacity: 0.2;
  }
  .main-image-container p {
    opacity: 0.2;
  }
}
