.nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 100vh;
  position: fixed; /* 추가 */
  z-index: 100; /* 추가: Nav를 다른 요소 위에 위치시킵니다. */
}

.nav-item {
  margin: 20px;
  color: white;
  cursor: pointer;
  font-size: 26px;
}
.nav-item:hover {
  transform: scale(1.4);
  transition: transform 0.3s;
}
